import {Injectable} from '@angular/core';
import {AdaptSvgIcon, AdaptSvgIconName} from '@bmc-ux/dpl-iconfont';

import {adaptWarn} from '../common/logger';

@Injectable({
  providedIn: 'root'
})
export class AdaptIconConfig {
  private _iconRegistry: Map<AdaptSvgIconName, string> = new Map<AdaptSvgIconName, string>();

  registerIcons(icons: AdaptSvgIcon[]): void {
    icons.forEach((icon) => {
      this._iconRegistry.set(icon.name, icon.data);
    });
  }

  getIcon(name: AdaptSvgIconName): string {
    const icon = this._iconRegistry.get(name) || '';

    if (!icon) {
      adaptWarn(`There is no registered icon with name: "${name}".
      Please make sure it has been added to register with AdaptIconConfig.registerIcons() method.`);
    }

    return this._iconRegistry.get(name);
  }
}
